.services-page {
  width: 100%;
}

/* Hero Section */
.services-hero {
  background: linear-gradient(135deg, #eff6ff 0%, #dbeafe 100%);
  padding: 5rem 2rem;
  text-align: center;
}

.services-hero-content h1 {
  font-size: 3rem;
  color: #0f172a;
  margin-bottom: 1rem;
}

.services-hero-subtitle {
  font-size: 1.375rem;
  color: #475569;
  max-width: 700px;
  margin: 0 auto;
}

/* Services Intro */
.services-intro {
  text-align: center;
  max-width: 800px;
  margin: 0 auto 4rem;
}

.services-intro h2 {
  margin-bottom: 1.5rem;
  color: #0f172a;
}

.services-intro p {
  font-size: 1.125rem;
  color: #475569;
  line-height: 1.8;
}

/* Services Grid */
.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
}

.service-card {
  padding: 2.5rem 2rem;
  text-align: center;
  border: 1px solid #e2e8f0;
}

.service-icon {
  width: 70px;
  height: 70px;
  margin: 0 auto 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #eff6ff 0%, #dbeafe 100%);
  border-radius: 16px;
  color: #3b82f6;
}

.service-card h3 {
  font-size: 1.25rem;
  margin-bottom: 1rem;
  color: #0f172a;
}

.service-card p {
  color: #64748b;
  line-height: 1.7;
}

/* Additional Services Section */
.services-additional h2 {
  text-align: center;
  margin-bottom: 3rem;
}

.additional-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.additional-card {
  padding: 2.5rem;
}

.additional-card h3 {
  color: #3b82f6;
  margin-bottom: 1rem;
  font-size: 1.5rem;
}

.additional-card p {
  color: #475569;
  line-height: 1.8;
}

/* CTA Section */
.services-cta {
  background: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
  padding: 5rem 2rem;
  text-align: center;
}

.services-cta .cta-content h2 {
  color: white;
  margin-bottom: 1rem;
}

.services-cta .cta-content p {
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.25rem;
  margin-bottom: 2rem;
}

.services-cta .btn-primary {
  background: white;
  color: #3b82f6;
}

.services-cta .btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(255, 255, 255, 0.3);
}

/* Responsive */
@media (max-width: 968px) {
  .services-hero-content h1 {
    font-size: 2.25rem;
  }

  .services-hero-subtitle {
    font-size: 1.125rem;
  }

  .services-grid {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }
}
