.home {
  width: 100%;
}

/* Hero Section */
.hero {
  position: relative;
  min-height: 600px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #eff6ff 0%, #dbeafe 100%);
  padding: 4rem 2rem;
  overflow: hidden;
}

.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url('https://images.unsplash.com/photo-1576091160550-2173dba999ef');
  background-size: cover;
  background-position: center;
  opacity: 0.08;
  z-index: 0;
}

.hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(59, 130, 246, 0.05) 0%, rgba(37, 99, 235, 0.08) 100%);
  z-index: 1;
}

.hero-content {
  position: relative;
  z-index: 2;
  max-width: 800px;
  text-align: center;
}

.hero-title {
  font-size: 3rem;
  font-weight: 700;
  color: #0f172a;
  margin-bottom: 1.5rem;
  line-height: 1.2;
}

.hero-name {
  color: #3b82f6;
}

.hero-subtitle {
  font-size: 1.375rem;
  color: #475569;
  margin-bottom: 2.5rem;
  line-height: 1.6;
}

.hero-buttons {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  flex-wrap: wrap;
}

.hero-buttons .btn-secondary {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

/* Section Header */
.section-header {
  text-align: center;
  margin-bottom: 4rem;
}

.section-header h2 {
  margin-bottom: 1rem;
}

.section-description {
  font-size: 1.25rem;
  color: #64748b;
  max-width: 600px;
  margin: 0 auto;
}

/* Features Grid */
.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.feature-card {
  text-align: center;
  padding: 3rem 2rem;
}

.feature-icon {
  width: 80px;
  height: 80px;
  margin: 0 auto 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #eff6ff 0%, #dbeafe 100%);
  border-radius: 20px;
  color: #3b82f6;
}

.feature-title {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: #0f172a;
}

.feature-description {
  color: #64748b;
  line-height: 1.6;
}

/* CTA Section */
.cta-section {
  background: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
  color: white;
  padding: 5rem 2rem;
  text-align: center;
}

.cta-content {
  max-width: 800px;
  margin: 0 auto;
}

.cta-title {
  font-size: 2.5rem;
  color: white;
  margin-bottom: 1rem;
}

.cta-description {
  font-size: 1.25rem;
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 2.5rem;
}

.cta-buttons {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  flex-wrap: wrap;
}

.cta-buttons .btn-primary {
  background: white;
  color: #3b82f6;
}

.cta-buttons .btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(255, 255, 255, 0.3);
}

.cta-buttons .btn-secondary {
  background: transparent;
  color: white;
  border-color: white;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.cta-buttons .btn-secondary:hover {
  background: white;
  color: #3b82f6;
}

/* Contact Grid */
.contact-grid {
  display: grid;
  grid-template-columns: 1fr 1.5fr;
  gap: 2rem;
}

.contact-info-card h3 {
  margin-bottom: 2rem;
  color: #0f172a;
}

.contact-item {
  display: flex;
  gap: 1rem;
  margin-bottom: 2rem;
  align-items: flex-start;
}

.contact-item svg {
  color: #3b82f6;
  flex-shrink: 0;
}

.contact-icon {
  font-size: 1.5rem;
  flex-shrink: 0;
}

.contact-label {
  font-size: 0.875rem;
  color: #64748b;
  margin-bottom: 0.25rem;
  font-weight: 500;
}

.contact-value {
  color: #0f172a;
  font-weight: 500;
  line-height: 1.6;
}

.contact-value a {
  color: #3b82f6;
  text-decoration: none;
  transition: color 0.2s ease;
}

.contact-value a:hover {
  color: #2563eb;
}

.map-container {
  height: 500px;
  padding: 0;
  overflow: hidden;
}

/* Responsive */
@media (max-width: 968px) {
  .hero-title {
    font-size: 2.25rem;
  }

  .hero-subtitle {
    font-size: 1.125rem;
  }

  .contact-grid {
    grid-template-columns: 1fr;
  }

  .map-container {
    height: 350px;
  }
}

@media (max-width: 640px) {
  .hero-buttons,
  .cta-buttons {
    flex-direction: column;
    align-items: stretch;
  }

  .hero-buttons .btn-primary,
  .hero-buttons .btn-secondary,
  .cta-buttons .btn-primary,
  .cta-buttons .btn-secondary {
    width: 100%;
    justify-content: center;
  }
}
