.reviews-page {
  width: 100%;
}

/* Hero Section */
.reviews-hero {
  background: linear-gradient(135deg, #eff6ff 0%, #dbeafe 100%);
  padding: 5rem 2rem;
  text-align: center;
}

.reviews-hero-content h1 {
  font-size: 3rem;
  color: #0f172a;
  margin-bottom: 1rem;
}

.reviews-hero-subtitle {
  font-size: 1.375rem;
  color: #475569;
  max-width: 700px;
  margin: 0 auto;
}

/* Stats Grid */
.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 2rem;
  max-width: 1000px;
  margin: 0 auto;
}

.stat-card {
  text-align: center;
  padding: 2.5rem 2rem;
  background: linear-gradient(135deg, #eff6ff 0%, #dbeafe 100%);
  border: none;
}

.stat-value {
  font-size: 2.5rem;
  font-weight: 700;
  color: #3b82f6;
  margin-bottom: 0.5rem;
}

.stat-label {
  font-size: 1rem;
  color: #475569;
  font-weight: 500;
}

/* Reviews Grid */
.reviews-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
}

.review-card {
  padding: 2rem;
  border: 1px solid #e2e8f0;
  transition: transform 0.3s ease, box-shadow 0.3s ease, border-color 0.3s ease;
}

.review-card:hover {
  border-color: #3b82f6;
  transform: translateY(-5px);
  box-shadow: 0 12px 24px rgba(59, 130, 246, 0.1);
}

.review-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1.25rem;
  gap: 1rem;
}

.review-name {
  font-size: 1.125rem;
  font-weight: 600;
  color: #0f172a;
  margin-bottom: 0.25rem;
}

.review-date {
  font-size: 0.875rem;
  color: #64748b;
  margin: 0;
}

.stars {
  display: flex;
  gap: 2px;
  flex-shrink: 0;
}

.review-text {
  color: #475569;
  line-height: 1.7;
  font-size: 1rem;
}

/* CTA Section */
.reviews-cta {
  background: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
  padding: 5rem 2rem;
  text-align: center;
}

.reviews-cta .cta-content h2 {
  color: white;
  margin-bottom: 1rem;
}

.reviews-cta .cta-content p {
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.25rem;
  margin-bottom: 2rem;
}

.reviews-cta .btn-primary {
  background: white;
  color: #3b82f6;
}

.reviews-cta .btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(255, 255, 255, 0.3);
}

/* Responsive */
@media (max-width: 968px) {
  .reviews-hero-content h1 {
    font-size: 2.25rem;
  }

  .reviews-hero-subtitle {
    font-size: 1.125rem;
  }

  .reviews-grid {
    grid-template-columns: 1fr;
  }

  .stat-value {
    font-size: 2rem;
  }
}
