.about-page {
  width: 100%;
}

/* Hero Section */
.about-hero {
  background: linear-gradient(135deg, #eff6ff 0%, #dbeafe 100%);
  padding: 5rem 2rem;
  text-align: center;
}

.about-hero-content h1 {
  font-size: 3rem;
  color: #0f172a;
  margin-bottom: 1rem;
}

.about-hero-subtitle {
  font-size: 1.5rem;
  color: #3b82f6;
  font-weight: 500;
}

/* Main Content */
.about-content {
  display: grid;
  grid-template-columns: 1fr 1.5fr;
  gap: 4rem;
  align-items: start;
}

.about-image-section {
  position: sticky;
  top: 100px;
}

.about-image-placeholder {
  width: 100%;
  aspect-ratio: 3/4;
  background: linear-gradient(135deg, #eff6ff 0%, #dbeafe 100%);
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.1);
}

.about-image-placeholder img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.about-text-section h2 {
  margin-bottom: 1.5rem;
  color: #0f172a;
}

.about-text-section p {
  margin-bottom: 1.5rem;
  line-height: 1.8;
  color: #475569;
}

/* Qualifications Grid */
.qualifications-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
}

.qualification-card {
  text-align: center;
  padding: 2.5rem 2rem;
}

.qualification-icon {
  width: 70px;
  height: 70px;
  margin: 0 auto 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #eff6ff 0%, #dbeafe 100%);
  border-radius: 16px;
  color: #3b82f6;
}

.qualification-card h3 {
  font-size: 1.25rem;
  margin-bottom: 0.75rem;
  color: #0f172a;
}

.qualification-card p {
  color: #64748b;
}

/* Philosophy Section */
.philosophy-section h2 {
  text-align: center;
  margin-bottom: 3rem;
}

.philosophy-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.philosophy-card {
  padding: 2.5rem;
}

.philosophy-card h3 {
  color: #3b82f6;
  margin-bottom: 1rem;
  font-size: 1.5rem;
}

.philosophy-card p {
  color: #475569;
  line-height: 1.8;
}

/* CTA Section */
.about-cta {
  background: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
  padding: 5rem 2rem;
  text-align: center;
}

.about-cta .cta-content h2 {
  color: white;
  margin-bottom: 1rem;
}

.about-cta .cta-content p {
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.25rem;
  margin-bottom: 2rem;
}

.about-cta .btn-primary {
  background: white;
  color: #3b82f6;
}

.about-cta .btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(255, 255, 255, 0.3);
}

/* Responsive */
@media (max-width: 968px) {
  .about-hero-content h1 {
    font-size: 2.25rem;
  }

  .about-hero-subtitle {
    font-size: 1.25rem;
  }

  .about-content {
    grid-template-columns: 1fr;
    gap: 3rem;
  }

  .about-image-section {
    position: relative;
    top: 0;
  }

  .about-image-placeholder {
    max-width: 400px;
    margin: 0 auto;
  }
}
