.footer {
  background: linear-gradient(180deg, #0f172a 0%, #1e293b 100%);
  color: white;
  padding: 4rem 2rem 2rem;
  margin-top: auto;
}

.footer-container {
  max-width: 1200px;
  margin: 0 auto;
}

.footer-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 3rem;
  margin-bottom: 3rem;
}

.footer-section {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.footer-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: white;
  margin-bottom: 0.25rem;
}

.footer-subtitle {
  font-size: 1rem;
  color: #3b82f6;
  font-weight: 500;
  margin-bottom: 0.5rem;
}

.footer-text {
  color: #cbd5e1;
  line-height: 1.6;
}

.footer-heading {
  font-size: 1.125rem;
  font-weight: 600;
  color: white;
  margin-bottom: 0.5rem;
}

.footer-info {
  display: flex;
  gap: 0.75rem;
  color: #cbd5e1;
  line-height: 1.6;
  align-items: flex-start;
}

.footer-info svg {
  flex-shrink: 0;
  margin-top: 0.25rem;
  color: #3b82f6;
}

.footer-info p {
  margin: 0;
  color: #cbd5e1;
  font-size: 0.95rem;
}

.footer-link {
  color: #cbd5e1;
  text-decoration: none;
  transition: color 0.2s ease;
}

.footer-link:hover {
  color: #3b82f6;
}

.footer-bottom {
  border-top: 1px solid #334155;
  padding-top: 2rem;
  text-align: center;
}

.footer-bottom p {
  color: #94a3b8;
  font-size: 0.875rem;
  margin: 0.5rem 0;
}

.footer-disclaimer {
  font-size: 0.8rem;
  color: #64748b;
}

@media (max-width: 768px) {
  .footer {
    padding: 3rem 1.5rem 1.5rem;
  }

  .footer-grid {
    gap: 2rem;
  }
}
