.contact-page {
  width: 100%;
}

/* Hero Section */
.contact-hero {
  background: linear-gradient(135deg, #eff6ff 0%, #dbeafe 100%);
  padding: 5rem 2rem;
  text-align: center;
}

.contact-hero-content h1 {
  font-size: 3rem;
  color: #0f172a;
  margin-bottom: 1rem;
}

.contact-hero-subtitle {
  font-size: 1.375rem;
  color: #475569;
  max-width: 700px;
  margin: 0 auto;
}

/* Contact Grid */
.contact-grid {
  display: grid;
  grid-template-columns: 1fr 1.5fr;
  gap: 2rem;
}

.contact-info-section {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.contact-info-card {
  padding: 2rem;
  text-align: center;
}

.contact-icon-wrapper {
  width: 60px;
  height: 60px;
  margin: 0 auto 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #eff6ff 0%, #dbeafe 100%);
  border-radius: 16px;
  color: #3b82f6;
}

.contact-info-card h3 {
  font-size: 1.25rem;
  color: #0f172a;
  margin-bottom: 0.75rem;
}

.contact-link {
  display: block;
  color: #3b82f6;
  font-weight: 600;
  font-size: 1.125rem;
  text-decoration: none;
  margin-bottom: 0.5rem;
  transition: color 0.2s ease;
}

.contact-link:hover {
  color: #2563eb;
}

.contact-description {
  color: #64748b;
  font-size: 0.95rem;
  margin: 0;
}

/* Office Hours */
.office-hours {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  margin-top: 1rem;
}

.hours-row {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 0;
  border-bottom: 1px solid #e2e8f0;
}

.hours-row:last-child {
  border-bottom: none;
}

.day {
  color: #475569;
  font-weight: 500;
}

.time {
  color: #3b82f6;
  font-weight: 600;
}

/* Map Section */
.map-section {
  height: 600px;
  padding: 0;
  overflow: hidden;
}

/* Additional Info */
.additional-info h2 {
  text-align: center;
  margin-bottom: 3rem;
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
}

.info-card {
  padding: 2rem;
}

.info-card h3 {
  color: #3b82f6;
  margin-bottom: 1rem;
  font-size: 1.25rem;
}

.info-card p {
  color: #475569;
  line-height: 1.7;
}

/* CTA Section */
.contact-cta {
  background: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
  padding: 5rem 2rem;
  text-align: center;
}

.contact-cta .cta-content h2 {
  color: white;
  margin-bottom: 1rem;
}

.contact-cta .cta-content p {
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.25rem;
  margin-bottom: 2rem;
}

.contact-cta .btn-primary {
  background: white;
  color: #3b82f6;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
}

.contact-cta .btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(255, 255, 255, 0.3);
}

/* Responsive */
@media (max-width: 968px) {
  .contact-hero-content h1 {
    font-size: 2.25rem;
  }

  .contact-hero-subtitle {
    font-size: 1.125rem;
  }

  .contact-grid {
    grid-template-columns: 1fr;
  }

  .map-section {
    height: 400px;
  }
}
